/* eslint-disable @typescript-eslint/no-unused-vars */
"use client"
import App from "next/app";
import Image from 'next/image';
import React, { ChangeEvent, useEffect, useState } from "react";



interface Message {
    topic: string;
    messages: unknown[];
}

interface AppData {
    messages: Message[];
    protocol: string;
}
export default function Home() {
    const [appState, setAppState] = useState<AppData>(
        {
            messages: [],
            protocol: "KAFKA"
        }
    );


    return (

        <div className="flex  flex-col  items-center justify-center h-screen  w-screen   bg-slate-500 space-y-2 ">
            <MainContainer appState={appState} setAppState={setAppState} />
        </div>);

}



interface AppProps {
    appState: AppData;
    setAppState: unknown;
}
function MainContainer(props: AppProps) {
    const [topicCount, setTopicCount] = useState(0);
    const topics: React.ReactNode[] = [];

    // Button Handler
    const addHandler = () => {
        // Copy app state
        // eslint-disable-next-line prefer-const
        let appStateCopy = { ...props.appState };

        // Append a new topic with empty messages
        appStateCopy.messages.push(
            {
                topic: "Default Topic " + topicCount.toString(),
                messages: []
            }
        )
        // Error checking
        if (typeof props.setAppState !== "function") {
            return;
        }
        // Set component state (just a simple topic counter)
        setTopicCount(topicCount + 1);
        // Set app state
        props.setAppState(appStateCopy);

    }

    // For every topic generate a TopicContainer
    if (typeof props.appState === "object" && props.appState !== null) {

        // Loop through each topic and create a TopicContainer
        for (let i = 0; i < props.appState.messages.length; i++) {
            topics.push(<TopicContainer
                key={props.appState.messages[i]["topic"]}
                appState={props.appState}
                setAppState={props.setAppState}
                topicID={props.appState.messages[i]["topic"]} />
            );
        }
    }


    return (
        <div className="flex flex-col overflow-y-scroll  w-1/3 space-y-2 bg-orange-100 rounded-md p-2">
            <div className="flex flex-col  space-y-2 w-full">
                {topics}
            </div>
            <ProtocolContainer appState={props.appState} setAppState={props.setAppState} />
            <div className="flex flex-row  justify-end w-full">
                <button className="btn btn-active btn-primary  rounded-md" onClick={addHandler}>Add Topic</button>
            </div>
            <button className="btn btn-active btn-primary object-none object-right-top" onClick={() => { console.log(JSON.stringify(props.appState)); }}>Send</button>
        </div>
    );
}



interface TopicContainerProps extends AppProps {
    topicID: string;
}
function TopicContainer(props: TopicContainerProps) {
    const [activeTopicID, setActiveTopicID] = useState<string>(props.topicID); // The topicID that is currently active (used for search and assign operations)
    const [currentTopicID, setCurrentTopicID] = useState<string>(props.topicID); // The new topicID that will replace the active one
    const [topicMessages, setTopicMessages] = useState<unknown[]>([]);
    const [generatedMessages, setGeneratedMessages] = useState<number>(0); // Count user generated messages

    // Run once to load the messages
    useEffect(() => {
        let temp: unknown[] = [];
        for (let i = 0; i < props.appState.messages.length; i++) {
            if (props.appState.messages[i]["topic"] === activeTopicID) {
                temp = props.appState.messages[i]["messages"];

                break;
            }
        }
        setTopicMessages(temp);
    }, []);

    // Message Display
    // eslint-disable-next-line prefer-const
    let displayMessages: React.ReactNode[] = [];
    if (Array.isArray(topicMessages)) {
        for (let i = 0; i < topicMessages.length; i++) {
            displayMessages.push(<SingleMessage key={activeTopicID + "_" + i.toString()} messageIndex={i} appState={props.appState} setAppState={props.setAppState} topicID={activeTopicID} />);
        }
    }

    // ============================== Handlers ==============================
    const topicChangeHandler = (event: React.ChangeEvent<HTMLInputElement>) => {
        setCurrentTopicID(event.target.value);
    }

    const saveHandler = () => {
        // check empty
        if (currentTopicID.trim().length === 0) {
            alert("Topic cannot be empty");
            return
        }
        setActiveTopicID(currentTopicID);
        // eslint-disable-next-line prefer-const
        let appStateCopy = { ...props.appState };
        for (let i = 0; i < appStateCopy.messages.length; i++) {
            if (appStateCopy.messages[i]["topic"] === activeTopicID) {
                appStateCopy.messages[i]["topic"] = currentTopicID;

                // Error checking
                if (typeof props.setAppState !== "function") {
                    return;
                }
                props.setAppState(appStateCopy);
            }
        }
    }

    const addMessageHandler = () => {
        // eslint-disable-next-line prefer-const
        let copyTopicMessages: unknown[] = [...topicMessages];

        copyTopicMessages.push({ "messageID": activeTopicID + "_MessageID_" + generatedMessages.toString() });

        // Component state
        setGeneratedMessages(generatedMessages + 1);
        setTopicMessages(copyTopicMessages);

        // App state
        let appStateCopy = { ...props.appState }
        for (let i = 0; i < appStateCopy.messages.length; i++) {
            if (appStateCopy.messages[i]['topic'] === activeTopicID) {
                appStateCopy.messages[i]['messages'] = copyTopicMessages
                break
            }
        }
        if (typeof props.setAppState === 'function') {
            props.setAppState(appStateCopy)
        }
    }
    // ============================== Handlers ==============================


    return (
        <div className="flex flex-col  space-x-2   border-2 border-gray-500 rounded-md p-2">
            <div className="flex flex-row  space-x-2 items-center">
                <label>Topic:</label>
                <input onChange={topicChangeHandler} type="text" value={currentTopicID} className="input input-bordered w-full max-w-xs bg-white" />
                <button className="btn btn-active btn-success rounded-md" onClick={saveHandler}>Save</button>
            </div>
            <br />
            <div className="flex flex-col items-start  space-y-2 w-full">
                <div className="grid grid-cols-2 gap-4 w-full items-center justify-between">
                    <div>
                        <label>Messages:</label>
                    </div>
                    <div className="justify-self-end">
                        <button className="btn btn-active btn-primary   " onClick={addMessageHandler}>Add</button>
                    </div>
                </div>
                <div className="w-full flex flex-col space-y-4">
                    {displayMessages}
                </div>
            </div>
        </div>
    )
}

interface SingleMessageProps extends AppProps {
    topicID: string;
    messageIndex: number;
}

function SingleMessage(props: SingleMessageProps) {
    const [activeMessageID, setActiveMessageID] = useState("-1");
    // First time load of messageID
    if (activeMessageID === "-1") {
        let thisTopicMessages: unknown[] = []
        // First find all topic messages
        for (let i = 0; i < props.appState.messages.length; i++) {
            if (props.appState.messages[i]["topic"] === props.topicID) {
                thisTopicMessages = [...props.appState.messages[i]['messages']]
            }
        }
        // Then search by index to find messageID
        for (let i = 0; i < thisTopicMessages.length; i++) {
            if (i === props.messageIndex) {

                if (typeof thisTopicMessages[i] === "object" && thisTopicMessages[i] !== null) {
                    if ("messageID" in thisTopicMessages[i]) {
                        setActiveMessageID(thisTopicMessages[i]['messageID']);
                        break;
                    }
                }

            }
        }
    }
    const messageID_Handler = (event: ChangeEvent<HTMLInputElement>) => {

        let appStateCopy: AppData = { ...props.appState }

        // Update app state
        for (const msg of appStateCopy['messages']) {
            if (msg['topic'] === props.topicID) {
                for (const singleMsg of msg['messages']) {
                    if (singleMsg['messageID'] === activeMessageID) {
                        singleMsg['messageID'] = event.target.value;
                    }
                }
            }
        }
        if (typeof props.appState === "function") {
            props.setAppState(appStateCopy)
        }
        setActiveMessageID(event.target.value)
    }


    return (
        <div id='SingleMessageContainer' className="flex flex-col w-full  ">
            <div id='messageIDContainer' className="flex flex-row space-x-2 w-full items-center">
                <label>MessageID:</label>
                <input type="text" value={activeMessageID} className="input input-bordered w-full max-w-xs bg-white input-sm" onChange={messageID_Handler} />
                <button className="btn btn-active btn-warning   " onClick={() => { console.log("Delete") }}>Delete</button>
            </div>
            <div id='fieldContainer' className="grid grid-cols-1">
                <label>This is a message!</label>
            </div>
            <div>
                <button className="btn btn-active btn-warning   " onClick={() => { console.log("Delete") }}>add</button>
            </div> 
        </div>
    )
}


function ProtocolContainer(props: AppProps) {
    const [selectedProtocol, setSelectedProtocol] = useState(props.appState.protocol);
    const AvailableProtocols = {
        KAFKA: "KAFKA",
        MQTT: "MQTT",
    }

    const protocolOptions: React.ReactNode[] = [];
    for (const protocol in AvailableProtocols) {
        protocolOptions.push(<option key={protocol} value={protocol}>{protocol}</option>);
    }

    const protocolChangeHandler = (event: React.ChangeEvent<HTMLSelectElement>) => {
        // eslint-disable-next-line prefer-const
        let appStateCopy = { ...props.appState };
        appStateCopy.protocol = event.target.value;
        setSelectedProtocol(event.target.value);
        // Error checking
        if (typeof props.setAppState !== "function") {
            return;
        }
        props.setAppState(appStateCopy);
    }
    return (
        <div className="flex flex-row items-center space-x-2">
            <label>Protocol:</label>
            <select className="select w-full max-w-xs bg-white" value={selectedProtocol} onChange={protocolChangeHandler}>
                {protocolOptions}
            </select>

        </div>
    );
}


